import Video from "./Video";

const LiveVideo = ({ data }) => {

    let videos = [];

    // Forsythe posts the videos in reverse order so the loop is backwards

    for(let i = data.length - 1; i >= 0; i--){
        let isLive = data[i].embed.badges.live.streaming;
        let isArchived = data[i].embed.badges.live.archived;

        if(!isLive && !isArchived) videos.push(data[i]);
    }


    if(videos.length === 0){
        return <h2 className="lightText">No upcoming streams</h2>;
    }else{
        return (
            <div className="videoGrid">
                {videos.map((video, i) => (
                    <Video key={i} title={video.name} videoEmbed={video.embed.html}/>
                ))}
            </div>
        );
    }
}

export default LiveVideo;