import { useRef, useEffect, useState, useCallback } from 'react';

// The built in Vimeo title dissappears when the video is less than 300px.
// The majority of this code checks the video size upon render and on resize to dynamically
// add a title

const Video = ({ title, videoEmbed }) => {
    const video = useRef(null);
    const [titleEl, setTitleEl] = useState(<></>);

    const checkSize = useCallback(() => {
        if(video.current && video.current.offsetWidth < 300){
            setTitleEl(<p className="tinyTitle">{ title }</p>);
        }else{
            setTitleEl(<></>);
        }
    }, [title]);

    useEffect(() => { 
        checkSize();
        window.addEventListener('resize', checkSize);
    }, [checkSize])

    let videoParams = videoEmbed.substring(videoEmbed.indexOf("https://player.vimeo.com/video/") + 31);
    videoParams = videoParams.substring(0, videoParams.indexOf("\""));

    if(videoEmbed === null) return (<></>);
    else{
        return(
            <div>
                <div ref={video} className="videoWrapper">
                    <iframe className="video" title={title} src={`https://player.vimeo.com/video/${videoParams}`} frameBorder="0" width="100%" height="100%" allowFullScreen></iframe>
                </div>

                { titleEl }
           </div>
        );
    }
}

export default Video