import Video from "./Video";

const LiveVideo = ({ data }) => {
    // only gets one stream (lastest one posted)
    // ask if he ever needs to display mutiple

    // also ask if he wants the thing as a popup

    let liveVids = [];

    for(let i = 0; i < data.length; i ++){
        let isLive = data[i].embed.badges.live.streaming;
        let isArchived = data[i].embed.badges.live.archived;

        if(isLive && !isArchived){
            liveVids.push(data[i]);
        }
    }

    if(liveVids.length === 0){
        return <h2 className="lightText">No live videos</h2>;
    }else{
        return( 
            <div className="videoGrid">
                {liveVids.map((video, i) => (
                    <Video key={i} title={video.name} videoEmbed={video.embed.html}/>
                ))}
            </div>
        );
    }
}

export default LiveVideo;