import './App.css';
import { useEffect, useState } from 'react';
import LiveVideo from './components/LiveVideo';
import UpcomingVideos from './components/UpcomingVideos';

const token = "5d5c157df9f684e811d74a303341da43";
const sconaChannelId = "82197283";

function App() {
	const [videos, setVideos] = useState([]);
    const [isLoaded, setIsLoaded] = useState(false);
    const [error, setError] = useState(null);

	useEffect(() => {
		fetch(`https://api.vimeo.com/users/${sconaChannelId}/videos?filter=live`, {
            method:'GET', 
            headers: {'Authorization': 'Bearer ' + token}
        })
        .then(res => res.json())
        .then(
            (result) => {
                setVideos(result.data);
				setIsLoaded(true);
            },
            (error) => {
                setIsLoaded(true);
                setError(Object.values(error));
            }    
        )
	}, [])

	function getContent(type){
		if(error){
            return <p className="lightText">Error: {error}</p>;
        } else if(!isLoaded){
            return <p className="lightText">Loading...</p>;
        } else if(type === "live"){
            return <LiveVideo data={videos}/>;
        } else if(type === "upcoming"){
			return <UpcomingVideos data={videos}/>;
		}
	}

	return (
		<>
			<div className="row">
				<div className="titleContainer">
					<h1 className="title notSelectable">
						Scona <span className="live">&nbsp;LIVE&nbsp;<span className="circle"></span>&nbsp;</span>
					</h1>
				</div>

				<div className="note titleContainer">
					<p>Please note that all the extra-curricular games in this gym are public events. Neither Strathcona High School nor Metro Athletics can control who is watching or recording the events; therefore, it is incumbent on all schools, coaches, parents, and athletes to know that the events in this gym may be live-streamed.</p>
				</div>
			</div>
				
			<div className="main titleContainer">
				<h1 className="subtitle">Live Videos</h1>
				{ getContent("live") }	
			</div>

			<div className="main titleContainer">
				<h1 className="subtitle">Upcoming Streams</h1>
				{ getContent("upcoming") }
			</div>

			<div className="row">
				<img className="mainImg" src='scona-live-2/SL1.png' alt="img" width="400"></img>

				<div className="column">
					<div className="columnEl">
						<h1 className="subtitle">Archived Videos</h1>
						<p className="lightText" style={{marginTop:"5px"}}>This year's archived videos will be available at the end of the season to view and download. All videos posted <a href="https://vimeo.com/scona">here</a>.</p>
						<p className="tinyTitle">Volleyball: &nbsp; <a href="https://vimeo.com/showcase/vb">2020</a> &nbsp; <a href="https://vimeo.com/showcase/7979544">2019</a></p>
						<p className="tinyTitle">Basketball: &nbsp; <a href="https://vimeo.com/showcase/7975600">2020</a> &nbsp; <a href="https://vimeo.com/showcase/7975600">2019</a> &nbsp; <a href="https://vimeo.com/showcase/7979553">2018</a></p>
						<p>&nbsp;</p>
					</div>

					<div className="columnEl">
						<img className="smallLogo" alt="Starthcona Highschool Logo" width="100%" src="scona-live-2/scona.png"></img>
					</div>
				</div>
			</div>

			<p className="footer">Â© Strathcona High School 2021 - Created by Finlay Soehn</p>
		</>
	);
}

export default App;
